import "./home.css"

//Imports
import { load } from './pages/load';
import { home_page } from './pages/home.js';
import { menu_page } from './pages/menu.js';
import { contact_page } from './pages/contact.js';
import { reservation_page } from './pages/reservation.js'

//Variables
const content = document.getElementById("content")
const navBar = document.getElementsByClassName("li header")

//Fresh page after page selection
const newPage = () => {
    const page = document.createElement('div')
    page.id = "page"
    content.appendChild(page)
}

//Page load 
window.addEventListener('load', function(){
    load()
})

//Nav bar menu selections
window.onload = function(){
        //Home Page
    navBar[0].addEventListener("click", function(){
        page.remove()
        newPage()
        home_page()
    }),
        //Menu Page
    navBar[1].addEventListener("click", function(){
        page.remove()
        newPage()
        menu_page()
    }),
        //Contact Page
    navBar[2].addEventListener("click", function(){
        page.remove()
        newPage()
        contact_page()
    }),
        //Reservation Page
    navBar[3].addEventListener("click", function(){
        page.remove()
        newPage()
        reservation_page()
    })
}

