export { load };
import { createElement } from './create.js';
import logo from '../../src/imgs/logo.svg'

const load = () => {
    const headerAll = createElement("header", "all-page", "header")
    content.appendChild(headerAll)

    const ul = createElement("ul", "ul", "header")
    headerAll.appendChild(ul)

    var text = ["Home", "Menu", "Contact", "Reservation"];
    for(var i = 0; i < text.length; i += 1) {
        const li = createElement("li", "li", "header")
        ul.appendChild(li)
        const a = createElement("a", "nav_bar", text[i])
        a.href = "#"
        li.appendChild(a)
        a.innerText = text[i];
    }

    const page = document.createElement('div')
    page.id = "page"
    content.appendChild(page)


    const mainPageDiv = createElement("div", "page", "div-holder")
    page.appendChild(mainPageDiv)

    const logoDiv = createElement("div", "main-page", "logo-holder")
    mainPageDiv.appendChild(logoDiv)

    const logoImg = createElement("img", "main-page", "logo")
    logoDiv.appendChild(logoImg)
    logoImg.setAttribute("src", logo);
}